import * as React from 'react';
import { useRef, useEffect } from 'react';
import Layout from '../components/Layout';
import styled from 'styled-components';

const StyledMain = styled.main``;
const IndexPage = () => {
  const canvasRef = useRef();
  const animateRef = useRef();
  const requestRef = useRef();
  const getDistance = (a, b) => {
    let changeX = (b.x - a.x) * (b.x - a.x);
    let changeY = (b.y - a.y) * (b.y - a.y);
    let d = Math.sqrt(changeX + changeY);
    return d;
  };
  useEffect(() => {
    let canvas = canvasRef.current;
    const setCanvasSize = () => {
      canvas.width = window.innerWidth * 2;
      canvas.height = window.innerHeight * 2;
      canvas.style.width = `${window.innerWidth}px`;
      canvas.style.height = `${window.innerHeight}px`;
    };
    canvas.style.position = 'fixed';
    canvas.style.top = 0;
    setCanvasSize();
    window.addEventListener('resize', setCanvasSize);
    const mousePos = { x: 0, y: 0 };
    const context = canvas.getContext('2d');
    // context.scale(2, 2);
    context.lineCap = 'round';
    context.strokeStyle = '#555555';
    const updateMousePos = (e) => {
      if (e.x) {
        mousePos.x = e.x * 2;
        mousePos.y = e.y * 2;
      } else {
        mousePos.x = e.touches[0].clientX * 2;
        mousePos.y = e.touches[0].clientY * 2;
      }
    };
    window.addEventListener('mousemove', updateMousePos);
    window.addEventListener('touchmove', updateMousePos);

    class Dot {
      constructor(posX, posY, velX, velY) {
        this.pos = {
          x: posX,
          y: posY,
        };
        this.color = 'rgba(0,0,0,0.2)';
        this.vel = {
          x: velX,
          y: velY,
        };
      }
      draw() {
        context.beginPath();
        context.arc(this.pos.x, this.pos.y, 3, 0, Math.PI * 2);
        context.fill();
      }
      update() {
        this.draw();
        if (this.pos.x > canvas.width || this.pos.x < 0) {
          this.vel.x *= -1;
        }
        if (this.pos.y > canvas.height || this.pos.y < 0) {
          this.vel.y *= -1;
        }
        this.pos.x += this.vel.x;
        this.pos.y += this.vel.y;
      }
    }

    let numOfDots = window.innerWidth / 10;

    let dotArray = [];

    for (let i = 0; i < numOfDots; i++) {
      dotArray.push(
        new Dot(
          Math.random() * canvasRef.current.width,
          Math.random() * canvasRef.current.height,
          Math.random() > 0.5 ? Math.random() * 5 : Math.random() * -5,
          Math.random() > 0.5 ? Math.random() * 5 : Math.random() * -5
        )
      );
    }

    animateRef.current = () => {
      context.fillStyle = 'rgba(255,255,255,0.03)';
      context.fillRect(0, 0, canvasRef.current.width, canvasRef.current.height);
      dotArray.forEach((dot, index, array) => {
        let d = getDistance(mousePos, dot.pos);
        if (d > canvas.width / 6) {
          context.fillStyle = `rgba(0,100,200,${d / 10000})`;
          context.strokeStyle = `rgba(0,100,200,${d / 10000})`;

          for (let i = 0; i < array.length; i++) {
            if (index !== i) {
              if (getDistance(array[i].pos, dot.pos) < 300) {
                context.beginPath();
                context.moveTo(array[i].pos.x, array[i].pos.y);
                context.lineTo(dot.pos.x, dot.pos.y);
                context.stroke();
                context.fill();
              }
            }
          }
        }
        dot.update();
      });

      requestRef.current = requestAnimationFrame(animateRef.current);
    };
    requestRef.current = requestAnimationFrame(animateRef.current);
    return () => {
      cancelAnimationFrame(requestRef.current);
      window.removeEventListener('mousemove', updateMousePos);
      window.removeEventListener('resize', setCanvasSize);
    };
  }, []);

  return (
    <Layout>
      <StyledMain>
        <canvas ref={canvasRef}></canvas>
      </StyledMain>
    </Layout>
  );
};

export default IndexPage;
